import { getAuthCodeApi } from '@/utils/httpUtils/api.js'
export default {
  data() {
    return {
      clientHeight: document.documentElement.clientHeight,
      phone: {
        code: false,
        name: '',
        timeOut: ''
      },
      status: {
        phoneNo: '',
        code:''
      }
    }
  },
  mounted() {
    window.onresize = () => {
      if (this.clientHeight > document.documentElement.clientHeight) {
        document.getElementById("wxlogin").style.display = "none";
      } else {
        document.getElementById("wxlogin").style.display = "block";
      }
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$common.Skip(this, '/Register')
    },
    goPwdSignin() {
      this.$common.Skip(this, '/PwdSignIn')
    },
    Login(){
      const c= res=>{
        console.log(res)
      }
      const param = {
        mobile:that.status.phoneNo,
        authCode:that.status.code
      }
      LoginApi(param).then(c)
    },
    getPhoneCode() {
      console.log('---')
      const that = this
      //false
      if (!that.phone.code) {
        if (!that.$checkVal.checkPhone(that.status.phoneNo)) {
          return
        }
        const c = res => {
          console.log(res)
          that.phone.code = true
          let codeNum = 10
          that.timeOut = setInterval(() => {
            codeNum--
            that.phone.name = '' + codeNum
            if (codeNum === 0) {
              clearInterval(that.timeOut);
              that.phone.code = false
              that.phone.name = ''
            }
          }, 1000);
        }
        const param = {
          mobile:that.status.phoneNo,
          authCodeType:1
        }
        getAuthCodeApi(param).then(c)
      }
    }
  }
}


