export function getAuthCodeApi(param) {
  const apiName = '/getAuthCode'
  return post(apiName, param)
}

//
export function RegisterApi(param) {
  const apiName = '/register'
  return post(apiName, param)
}

//
export function LoginApi(param){
  const apiName = '/login'
  return post(apiName, param)
}


